import React from "react";
import Title from "./Title";
import harryMain from "../assets/img/harry_main.png";
import harryIntro from "../assets/img/harry_intro.png"
import harryProfessor from "../assets/img/harry_professor.png"
import harryHistory from "../assets/img/harry_history.png"
import { useLightMode } from "./ModeContext";

function PageImage({ src, alt }) {
  return (
    <div className="harry_img">
      <img src={src} alt={alt} />
    </div>
  );
}

function PageDetail({ title, detail, href }) {
  return (
    <div className="detail_wrap">
      <div className="title">
        <h3>{title}</h3>
        <a href={href} className="arrow_wrap">
          
          <span class="material-symbols-rounded">arrow_forward</span>
        </a>
      </div>
      <div className="detail">
        <pre>{detail}</pre>
      </div>
    </div>
  );
}

function Harrypotter() {
  const mainConcept = `     ''   . 
   4                 .    ,            .
      !`;
  const subConcept01 = `     .         .
          .    .`;
  const subConcept02 = `    .    ,        .
      .`;
  const subConcept00 = `      .             .
css      ,          .
    .`;

  const { isLightMode } = useLightMode();
  return (
    <div id="harry_wrap" className={isLightMode ? "light" : "dark"}>
      <Title title={"   :  "} />
      <div className="harry_detail_wrap">
        <div className="harry_detail">
          <PageImage src={harryMain} alt={" "} />
          <PageDetail
            title={" "}
            detail={mainConcept}
            href={"https://jihana030.github.io/Hogwarts_School/views/index.html"}
          />
        </div>
        <p>&#91;   &#93;</p>
        <div className="harry_detail">
          <PageImage src={harryIntro} alt={" "} />
          <PageDetail
            title={" "}
            detail={subConcept00}
            href={"https://jihana030.github.io/Hogwarts_School/"}
          />
        </div>
        <div className="harry_detail">
          <PageImage src={harryHistory} alt={" "} />
          <PageDetail
            title={" "}
            detail={subConcept01}
            href={"https://jihana030.github.io/Hogwarts_School/views/history.html"}
          />
        </div>
        <div className="harry_detail">
          <PageImage src={harryProfessor} alt={" "} />
          <PageDetail
            title={" "}
            detail={subConcept02}
            href={"https://jihana030.github.io/Hogwarts_School/views/professor.html"}
          />
        </div>
      </div>
    </div>
  );
}

export default Harrypotter;
