import React from "react";
import { useState } from "react";
import drawImg from "../assets/img/luckydraw.png";
import drawImgMove from "../assets/img/luckydraw2.gif";
import SlideTxt from "./SlideTxt";
import RandomNum from "./RandomNum";
import Title from "./Title";
import { useLightMode } from "./ModeContext";

function Luckydraw() {
  const [imgMove, setImgMove] = useState(false); //false , move  
  const [randomTxt, setRandomTxt] = useState(false); //setTimeout   
  const [num] = useState(parseInt(Math.random() * 10));
  const [txt, setTxt] = useState("");
  const luckyTxt = () => {
    const txtArr = [
      "   !",
      `  ${num}  ?`,
      `  ${num}  ?`,
      `  ${num}  ?`,
      `  ${num}  ?`,
      `  ${num}  ?`,
    ];
    setRandomTxt(true);
    if (num === 0) {
      setTxt(txtArr[0]);
    } else {
      const random = parseInt(Math.random() * (txtArr.length - 1) + 1);
      setTxt(txtArr[random]);
    }
  };

  const handleLucky = () => {
    setImgMove(true);
    const timer = setTimeout(() => {
      luckyTxt();
    }, 8000);
    return () => clearTimeout(timer);
  };
  const { isLightMode } = useLightMode();
  return (
    <div id="luckydraw_wrap" className={isLightMode ? "light" : "dark"}>
      <Title title="" />
      <div className="lucky_wrap">
        <div className="draw">
          {imgMove ? (
            <img className="draw_gif" src={drawImgMove} alt="" />
          ) : (
            <img className="draw_png" src={drawImg} alt="" onClick={handleLucky} />
          )}
        </div>
        {imgMove && <RandomNum num={num} txt={txt} time={randomTxt} />}
      </div>
      <SlideTxt />
    </div>
  );
}

export default Luckydraw;
