import React from "react";
import makingImg from '../assets/img/making.gif'
import Title from "./Title";

function Making() {
  return (
    <div id="making_wrap">
      <Title title={' .'}/>
      <div className="making">
        <img src={makingImg} alt={''} />
      </div>
    </div>
  )
}
export default Making;